/* Reset some default browser styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Basic styles for the app */
.App {
  text-align: center;
  font-family: Arial, sans-serif;
  background-color: #ffffff;
  height: 100vh; /* Full height */
  display: flex;
  flex-direction: column;
  justify-content: center; /* Center content vertically */
}

/* Hide header styles */
.App-header {
  display: none; /* Hide header completely */
}

/* Mobile sign out button styles */
.mobile-signout-button {
  position: absolute;
  top: 10px; /* Position at the top */
  right: 20px; /* Position at the right */
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 5px 10px; /* Smaller padding for mobile */
  cursor: pointer;
  transition: background-color 0.3s;
  z-index: 1000; /* Ensure it is above other elements */
}

.mobile-signout-button:hover {
  background-color: #0056b3;
}

/* Button styles */
button {
  background-color: #007bff;
  border: none;
  color: white;
  padding: 10px 15px;
  margin: 5px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #0056b3;
}

/* Login buttons container styles */
.login-buttons-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Full height of the viewport */
  margin: 0;
  padding: 0;
}

/* Logo styles */
.logo {
  width: 150px; /* Default width of the logo */
  margin-bottom: 20px; /* Space between logo and buttons */
  margin-top: 100px;
}

/* Responsive styles for desktop */
@media (min-width: 769px) {
  .login-buttons-container {
    flex-direction: row; /* Align buttons in a row on desktop */
    justify-content: center;
  }

  .logo {
    width: 300px; /* Larger logo on desktop */
    margin-bottom: 40px;
  }

  .login-buttons-container button {
    margin: 0 10px; /* Horizontal spacing between buttons */
    width: 200px;
  }
}

/* Chat Room styles */
section {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1; /* Allow section to grow and take available space */
  overflow: hidden; /* Prevent overflow */
}

/* Messages container */
.messages-container {
  flex-grow: 1; /* Allow it to take the remaining space */
  overflow-y: auto; /* Allow scrolling of messages */
  width: 100%; /* Full width */
  padding: 10px; /* Padding for chat messages */
}

/* Form styles */
.message-form {
  display: flex;
  align-items: flex-start; /* Align items to the start */
  margin-top: 0;
  padding: 10px;
  background-color: #ffffff;
  position: relative; /* To position the word counter outside the input */
}

/* Adjust input styles */
textarea {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  flex-grow: 1;
  height: 40px;
  width: 500px;
  max-height: 150px; /* Limit max height for expanded textarea */
  min-height: 40px; /* Minimum height for better UX */
  margin-right: 5px; /* Space between textarea and button */
  overflow-y: auto; /* Allow scrolling in the textarea */
  resize: none; /* Prevent manual resizing */
}

/* Send button styles */
.send-button {
  background: transparent;
  border: none;
  color: #007bff;
  font-size: 1.5rem; /* Larger font size for the emoji */
  cursor: pointer;
  padding: 0;
  margin: 0;
  height: 40px; /* Align with input height */
}

/* Word counter styles */
.word-counter {
  position: absolute; /* Position it below the form */
  bottom: 10px; /* Space it below the input */
  right: 20px; /* Align it to the right */
  font-size: 12px;
  color: #888;
}

/* Chat message styles */
.ChatMessage {
  display: flex;
  align-items: center;
  margin: 10px 0;
  text-align: justify;
  word-break: break-word; /* Allow long words to break */
}

/* Profile picture styles */
.profile-image {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 10px; /* Spacing from the message */
}

/* Anonymous image styles */
.anonymous-image {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #007bff; /* Same background color as profile image */
  color: white;
  font-weight: bold;
}

/* Responsive styles */
@media (max-width: 768px) {
  .login-buttons-container {
    flex-direction: column;
  }

  .login-buttons-container button {
    margin: 10px 0;
    width: 100%; /* Full width on mobile */
  }

  .message-form {
    flex-direction: row; /* Keep form elements in row */
    padding: 10px 10px 30px; /* Extra padding for the counter */
    height: auto; /* Allow height to adjust based on input */
  }

  textarea {
    width: 90%; /* Adjust input width on mobile */
    max-width: 85%; /* Set a max width for responsiveness */
  }

  .send-button {
    width: 20%; /* Adjust send button width */
    font-size: 1.5rem; /* Maintain emoji size */
  }

  .mobile-signout-button {
    display: block; /* Show on mobile */
    right: 10px; /* Position at the right */
  }

  .word-counter {
    bottom: 10px; /* Position below the textarea */
    font-size: 12px;
    margin-right: 10px; /* Right margin for spacing */
  }
}
